:root {
    --near-black-green: #283812;
    --near-white-green: #e6f5d0;
    --medium-green: #73AD21;
    --bit-deeper-green: #497013;
    --keys: #c3e396;
    --links: #8ec147;
}

h1 {
    color: black;
    text-align: center;
    font-family: "Roboto", sans-serif;
    font-size: 50px;
    font-weight: 200;
    word-break: break-all;
}

h2 {
    color: var(--bit-deeper-green);
    text-align: left;
    font-family: "Roboto", sans-serif;
    font-size: 22px;
    font-weight: bold;
    word-break: break-all;
}

h3 {
    color: black;
    text-align: center;
    font-family: "Roboto", sans-serif;
    font-size: 20px;
    font-weight: 150;
    word-break: break-all;
}

code.inline {
    background: var(--keys);
    color: var(--near-black-green);
    padding: 0.1em;
    border-radius: 0.3em;
    /*font-family: "Courier New", Courier, monospace;*/
}

.realcode {
    background-color: #f4fbea;
    border: 1px solid var(--medium-green);
    display: block;
    padding: 10px;
    font-family: "Courier New", Courier, monospace;
    border-radius: 0.3em;
}
span.directory {
    display: inline;
    color: #f4fbea;
    padding: 0.1em;
    background-color: var(--bit-deeper-green);
    border-radius: 0.3em;
    font-family: "Courier New", Courier, monospace;
}

span.monospace {
    font-family: "Courier New", Courier, monospace;
}
.box {
    margin: auto;
    width: 51%;
    border: 1px solid var(--medium-green);
    padding: 10px;
    border-radius: 1em;
}

.write{
    border:none;
	padding: 7px;
	background: var(--bit-deeper-green);
	color:white;
}


body {
    background: var(--near-white-green);
}

* {
    font-family: "Montserrat", sans-serif;
    line-height: 1.5;
}

a:link {
    color: var(--links);
    text-decoration: underline;
}

.button {
	border-radius: 2em;
    cursor:pointer;
    margin: 5px;
    background: var(--bit-deeper-green);
    color: white;
}

.home {
    border: none;
    font-size: 20px;
}
.button:hover {
	box-shadow: 0 3px 7px 0 var(--medium-green);
}
